.App {
  width: 100vw;
  font-family: "Montserrat", sans-serif;
}

.App-header {
  width: 100%;
  height: 100vh;
  padding-top: 3%;
  padding-right: 5%;
  padding-left: 5%;
  text-shadow: 0px 0px 2px black;
  color: white;
  background-image: url("HorizontalImage.jpg");
  background-position: center;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Personal-Info {
  padding-top: 5%;
  width: 100%;
  position: sticky;
  top: 0px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

h1 {
  font-weight: bold;
  font-size: 350%;
  margin-bottom: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}

h2 {
  font-size: 200%;
  margin-bottom: 5px;
}

.aboutMe {
  padding-top: 5%;
  width: 95%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.aboutMe > h2 {
  font-size: 200%;
  font-weight: bold;
}

.education {
  width: 85%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.education > h3 {
  margin-bottom: 3%;
  font-size: 150%;
  text-decoration: underline;
}

.Projects {
  padding-top: 5%;
  width: 95%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Projects > h2 {
  font-size: 200%;
  font-weight: bold;
}

.CriticalFail {
  height: 50vh;
  width: 95%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.CriticalFail > h3 {
  width: 95%;
  margin-bottom: 3%;
  font-size: 150%;
  text-decoration: underline;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
}

.demo-display {
  display: flex;
  justify-content: center;
  align-items: center;
}

img {
  width: 50%;
}

.Pinfo {
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.Pinfo > ul {
  height: 75%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.links {
  color: black;
  background-color: white;
  border-radius: 10%;
}

.links:hover {
  transition: 1000ms;
  background-color: aqua;
}

ul {
  list-style-type: disc;
}

footer {
  height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

footer > div {
  margin-bottom: 10px;
  width: 65%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

@media screen and (max-width: 450px), screen and (max-height: 600px) {
  .App-header {
    background-image: url("verticalImage.jpg");
    background-position: center;
    background-size: cover;
    text-shadow: 0px 0px 5px white;
    color: black;
    transition: 0.5s;
  }

  h1 {
    font-size: 250%;
  }

  h2 {
    font-size: 125%;
  }

  .CriticalFail {
    height: 60vh;
  }

  .demo-display {
    flex-direction: column;
  }

  .Pinfo {
    width: 90%;
    margin-bottom: 3%;
  }

  .Pinfo > h4 {
    padding-top: 3%;
  }

  img {
    padding-top: 3%;
    width: 100%;
  }
}
